'use client';

import React, { useEffect, useState } from 'react';
import { useAuth } from '../context/AuthContext';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { 
  Briefcase, DollarSign, TrendingUp, Users, 
  PlusCircle, ArrowUpRight, Clock, Award,
  BarChart, Target, Zap, CheckCircle
} from 'lucide-react';
import { gigAPI, bidAPI } from '../services/api';

interface DashboardStats {
  totalGigs: number;
  activeGigs: number;
  totalBids: number;
  earnings: number;
}

interface RecentActivity {
  id: string;
  type: 'bid' | 'gig';
  title: string;
  description: string;
  time: string;
  status: 'success' | 'pending' | 'rejected';
}

export default function DashboardPage() {
  const { user, isAuthenticated, loading } = useAuth();
  const router = useRouter();
  const [stats, setStats] = useState<DashboardStats>({
    totalGigs: 0,
    activeGigs: 0,
    totalBids: 0,
    earnings: 0,
  });
  const [recentActivities, setRecentActivities] = useState<RecentActivity[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    if (!loading && !isAuthenticated) {
      router.push('/login');
      return;
    }

    if (isAuthenticated) {
      fetchDashboardData();
    }
  }, [isAuthenticated, loading, router]);

  const fetchDashboardData = async () => {
    try {
      // In a real app, you'd have a dedicated dashboard endpoint
      // For now, we'll fetch data from multiple endpoints
      const [gigsResponse, bidsResponse] = await Promise.all([
        gigAPI.getGigs(),
        bidAPI.getUserBids(),
      ]);

      const userGigs = gigsResponse.data.data.filter(
        (gig: any) => gig.ownerId?._id === user?._id
      );

      setStats({
        totalGigs: userGigs.length,
        activeGigs: userGigs.filter((gig: any) => gig.status === 'open').length,
        totalBids: bidsResponse.data.count || 0,
        earnings: 0, // You would calculate this from completed projects
      });

      // Mock recent activities
      setRecentActivities([
        {
          id: '1',
          type: 'bid',
          title: 'Web Development Project',
          description: 'Your bid was accepted',
          time: '2 hours ago',
          status: 'success',
        },
        {
          id: '2',
          type: 'gig',
          title: 'Logo Design Needed',
          description: 'New bid received',
          time: '5 hours ago',
          status: 'pending',
        },
        {
          id: '3',
          type: 'bid',
          title: 'Mobile App Development',
          description: 'Bid submitted successfully',
          time: '1 day ago',
          status: 'pending',
        },
        {
          id: '4',
          type: 'gig',
          title: 'E-commerce Website',
          description: 'Project completed successfully',
          time: '2 days ago',
          status: 'success',
        },
      ]);
    } catch (error) {
      console.error('Error fetching dashboard data:', error);
    } finally {
      setIsLoading(false);
    }
  };

  if (loading || isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-primary-500"></div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-gray-50 to-white py-12">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Welcome Header */}
        <div className="mb-10">
          <div className="flex flex-col md:flex-row md:items-center justify-between gap-6">
            <div>
              <h1 className="text-4xl font-bold text-gray-900 mb-2">
                Welcome back, <span className="gradient-text">{user?.name}!</span>
              </h1>
              <p className="text-gray-600 text-lg">
                Here&apos;s what&apos;s happening with your account today.
              </p>
            </div>
            <Link
              href="/gigs/create"
              className="btn-primary inline-flex items-center group"
            >
              <PlusCircle className="w-5 h-5 mr-2 group-hover:rotate-90 transition-transform" />
              Post New Gig
            </Link>
          </div>
        </div>

        {/* Stats Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-12">
          <div className="glass-card rounded-2xl p-6 card-hover">
            <div className="flex items-center justify-between mb-4">
              <div className="p-3 rounded-xl bg-gradient-to-br from-blue-100 to-blue-200">
                <Briefcase className="w-6 h-6 text-blue-600" />
              </div>
              <TrendingUp className="w-5 h-5 text-green-500" />
            </div>
            <h3 className="text-3xl font-bold text-gray-900 mb-2">{stats.totalGigs}</h3>
            <p className="text-gray-600 font-medium">Total Gigs Posted</p>
          </div>

          <div className="glass-card rounded-2xl p-6 card-hover">
            <div className="flex items-center justify-between mb-4">
              <div className="p-3 rounded-xl bg-gradient-to-br from-green-100 to-green-200">
                <Target className="w-6 h-6 text-green-600" />
              </div>
              <ArrowUpRight className="w-5 h-5 text-blue-500" />
            </div>
            <h3 className="text-3xl font-bold text-gray-900 mb-2">{stats.activeGigs}</h3>
            <p className="text-gray-600 font-medium">Active Gigs</p>
          </div>

          <div className="glass-card rounded-2xl p-6 card-hover">
            <div className="flex items-center justify-between mb-4">
              <div className="p-3 rounded-xl bg-gradient-to-br from-purple-100 to-purple-200">
                <Users className="w-6 h-6 text-purple-600" />
              </div>
              <Zap className="w-5 h-5 text-yellow-500" />
            </div>
            <h3 className="text-3xl font-bold text-gray-900 mb-2">{stats.totalBids}</h3>
            <p className="text-gray-600 font-medium">Total Bids</p>
          </div>

          <div className="glasscard rounded-2xl p-6 card-hover">
            <div className="flex items-center justify-between mb-4">
              <div className="p-3 rounded-xl bg-gradient-to-br from-orange-100 to-orange-200">
                <DollarSign className="w-6 h-6 text-orange-600" />
              </div>
              <BarChart className="w-5 h-5 text-primary-500" />
            </div>
            <h3 className="text-3xl font-bold text-gray-900 mb-2">${stats.earnings}</h3>
            <p className="text-gray-600 font-medium">Total Earnings</p>
          </div>
        </div>

        <div className="grid lg:grid-cols-3 gap-8">
          {/* Quick Actions */}
          <div className="lg:col-span-2">
            <div className="glass-card rounded-2xl p-6 mb-8">
              <h2 className="text-2xl font-bold text-gray-900 mb-6">Quick Actions</h2>
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                <Link
                  href="/gigs"
                  className="p-6 rounded-xl border-2 border-primary-200 hover:border-primary-300 bg-gradient-to-r from-primary-50 to-white hover:from-primary-100 transition-all duration-300 group"
                >
                  <div className="flex items-center space-x-4">
                    <div className="p-3 rounded-lg bg-primary-500 group-hover:scale-110 transition-transform">
                      <Briefcase className="w-6 h-6 text-white" />
                    </div>
                    <div>
                      <h3 className="font-semibold text-gray-900">Browse Gigs</h3>
                      <p className="text-sm text-gray-600">Find new opportunities</p>
                    </div>
                  </div>
                </Link>

                <Link
                  href="/my-bids"
                  className="p-6 rounded-xl border-2 border-accent-200 hover:border-accent-300 bg-gradient-to-r from-accent-50 to-white hover:from-accent-100 transition-all duration-300 group"
                >
                  <div className="flex items-center space-x-4">
                    <div className="p-3 rounded-lg bg-accent-500 group-hover:scale-110 transition-transform">
                      <Award className="w-6 h-6 text-white" />
                    </div>
                    <div>
                      <h3 className="font-semibold text-gray-900">My Bids</h3>
                      <p className="text-sm text-gray-600">Track your applications</p>
                    </div>
                  </div>
                </Link>

                <Link
                  href="/my-gigs"
                  className="p-6 rounded-xl border-2 border-green-200 hover:border-green-300 bg-gradient-to-r from-green-50 to-white hover:from-green-100 transition-all duration-300 group"
                >
                  <div className="flex items-center space-x-4">
                    <div className="p-3 rounded-lg bg-green-500 group-hover:scale-110 transition-transform">
                      <CheckCircle className="w-6 h-6 text-white" />
                    </div>
                    <div>
                      <h3 className="font-semibold text-gray-900">My Gigs</h3>
                      <p className="text-sm text-gray-600">Manage your projects</p>
                    </div>
                  </div>
                </Link>

                <div className="p-6 rounded-xl border-2 border-purple-200 bg-gradient-to-r from-purple-50 to-white">
                  <div className="flex items-center space-x-4">
                    <div className="p-3 rounded-lg bg-purple-500">
                      <BarChart className="w-6 h-6 text-white" />
                    </div>
                    <div>
                      <h3 className="font-semibold text-gray-900">Analytics</h3>
                      <p className="text-sm text-gray-600">View performance insights</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            {/* Recent Activities */}
            <div className="glass-card rounded-2xl p-6">
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-2xl font-bold text-gray-900">Recent Activities</h2>
                <Clock className="w-5 h-5 text-gray-400" />
              </div>
              <div className="space-y-4">
                {recentActivities.map((activity) => (
                  <div
                    key={activity.id}
                    className="flex items-center p-4 rounded-lg border border-gray-200 hover:bg-gray-50 transition-colors"
                  >
                    <div className={`w-10 h-10 rounded-full flex items-center justify-center mr-4 ${
                      activity.type === 'bid' 
                        ? 'bg-gradient-to-br from-blue-100 to-blue-200' 
                        : 'bg-gradient-to-br from-green-100 to-green-200'
                    }`}>
                      {activity.type === 'bid' ? (
                        <Award className="w-5 h-5 text-blue-600" />
                      ) : (
                        <Briefcase className="w-5 h-5 text-green-600" />
                      )}
                    </div>
                    <div className="flex-1">
                      <h4 className="font-medium text-gray-900">{activity.title}</h4>
                      <p className="text-sm text-gray-600">{activity.description}</p>
                    </div>
                    <div className="text-right">
                      <span className="text-sm text-gray-500">{activity.time}</span>
                      <div className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium mt-1 ${
                        activity.status === 'success' 
                          ? 'bg-green-100 text-green-800'
                          : activity.status === 'pending'
                          ? 'bg-yellow-100 text-yellow-800'
                          : 'bg-red-100 text-red-800'
                      }`}>
                        {activity.status.charAt(0).toUpperCase() + activity.status.slice(1)}
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {/* Profile & Tips */}
          <div>
            {/* Profile Card */}
            <div className="glass-card rounded-2xl p-6 mb-8">
              <div className="flex flex-col items-center text-center mb-6">
                <div className="w-24 h-24 rounded-full bg-gradient-to-br from-primary-500 to-accent-600 flex items-center justify-center mb-4 shadow-lg">
                  <span className="text-white text-3xl font-bold">
                    {user?.name?.charAt(0).toUpperCase()}
                  </span>
                </div>
                <h3 className="text-xl font-bold text-gray-900 mb-2">{user?.name}</h3>
                <p className="text-gray-600 mb-4">{user?.email}</p>
                <div className="px-4 py-2 bg-gradient-to-r from-primary-100 to-accent-100 rounded-full">
                  <span className="text-sm font-medium text-primary-700">Verified Member</span>
                </div>
              </div>
              
              <div className="space-y-4">
                <div className="flex justify-between items-center">
                  <span className="text-gray-600">Member Since</span>
                  <span className="font-medium">2024</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-gray-600">Profile Completion</span>
                  <div className="flex items-center">
                    <div className="w-32 h-2 bg-gray-200 rounded-full overflow-hidden">
                      <div className="h-full bg-gradient-to-r from-primary-500 to-accent-500 w-3/4"></div>
                    </div>
                    <span className="ml-2 font-medium">75%</span>
                  </div>
                </div>
              </div>
            </div>

            {/* Tips Card */}
            <div className="glass-card rounded-2xl p-6 bg-gradient-to-br from-blue-50 to-cyan-50 border border-blue-200">
              <h3 className="text-xl font-bold text-gray-900 mb-4">Tips for Success</h3>
              <div className="space-y-4">
                <div className="flex items-start space-x-3">
                  <div className="w-6 h-6 rounded-full bg-blue-500 flex items-center justify-center flex-shrink-0 mt-0.5">
                    <span className="text-white text-xs font-bold">1</span>
                  </div>
                  <p className="text-sm text-gray-700">
                    Complete your profile to attract more clients
                  </p>
                </div>
                <div className="flex items-start space-x-3">
                  <div className="w-6 h-6 rounded-full bg-blue-500 flex items-center justify-center flex-shrink-0 mt-0.5">
                    <span className="text-white text-xs font-bold">2</span>
                  </div>
                  <p className="text-sm text-gray-700">
                    Write detailed proposals for better chances
                  </p>
                </div>
                <div className="flex items-start space-x-3">
                  <div className="w-6 h-6 rounded-full bg-blue-500 flex items-center justify-center flex-shrink-0 mt-0.5">
                    <span className="text-white text-xs font-bold">3</span>
                  </div>
                  <p className="text-sm text-gray-700">
                    Respond quickly to client messages
                  </p>
                </div>
              </div>
              
              <div className="mt-6 pt-6 border-t border-blue-200">
                <Link
                  href="/profile/edit"
                  className="text-sm font-medium text-primary-600 hover:text-primary-700 inline-flex items-center"
                >
                  Improve your profile
                  <ArrowUpRight className="w-4 h-4 ml-1" />
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}